import {ComponentClass} from 'react'

export interface MultiLanguageAttribute {
    jp: string,
    cn: string,
    en: string
}

export type SupportedLanguages = keyof MultiLanguageAttribute

export const LanguageNames: MultiLanguageAttribute = {
    jp: '',
    cn: '',
    en: 'English'
}

type BloodType = 'A' | 'B' | 'AB' | 'O'

export interface MemberInfo {
    name: MultiLanguageAttribute
    birthday: string
    role?: string
    CVName: MultiLanguageAttribute | MultiLanguageAttribute[] // Roselia
    bloodType?: BloodType
    zodiac?: Zodiac
    encoreColor: string
    external?: ExternalText[]
    [attrs: string]: any
}

interface LinkInfo {
    description: string
    link: string
}

interface ExternalText { // 
    title: string
    content: string | string[] | MultiLanguageAttribute
    hidden?: boolean
}

export type TrackType = 'single' | 'album' | 'cover' | 'ost'

export interface TrackInfo {
    id: number
    displayId?: string
    type?: TrackType
    title: string
    track: string[]
    releaseDate: string
    links?: LinkInfo[]
    external?: ExternalText[],
    hasLimitedEdition?: boolean
    [attrs: string]: any
}

export type ExternalLinks = LinkInfo[]

export interface RecommandVideoAdapter {
    source: string,
    videos?: RecommandVideo
    getLink(s: string): string
}

export interface RecommandVideo {
    name: string
    videos: VideoDetail[]
    authors: string[]
    [attr: string]: any
}

interface VideoDetail {
    title: string
    author: string
    coverImage: string
    aid: string
}

export interface BannerImageConfig {
    background?: string
    foreground?: string,
    gradient?: string
    blurBackground?: boolean
}

export interface SiteImageGetter {
    trackImageGetter (t: TrackInfo): string
    limitedTrackImageGetter (t: TrackInfo): string
    memberImageGetter (m: MemberInfo): string
    cvImageGetter (m: MemberInfo): string
}

export interface SitePlugins {
    [attribute: string]: ComponentClass
}

export interface ExternalTrackList {
    displayName: MultiLanguageAttribute,
    trackType: TrackType,
    trackList: TrackInfo[]
}

export interface BangumiItem {
    name: string
    link: string
    coverImage: string
    releaseDate: string
    meta?: string
}

export interface SiteConfig {
    title: string,
    configName: string
    playerUrl?: string
    siteLogo: string
    siteFavicon: string
    logoSpin?: boolean
    members: MemberInfo[]
    singles: TrackInfo[]
    albums?: TrackInfo[]
    covers?: TrackInfo[]
    videos?: RecommandVideoAdapter[]
    themeColor: string
    bannerImage: BannerImageConfig,
    getters: SiteImageGetter,
    plugins: SitePlugins,
    externalTrackLists?: ExternalTrackList[]
    bangumiList?: BangumiItem[],
    externalLinks?: ExternalLinks
    [attr: string]: any
}

export interface SiteOverallConfig {
    encoreSites: {
        site: SiteConfig,
        path: string
    }[],
    defaultSite: SiteConfig
}


export const ZodiacDict = {
    'aries': { 'cn': '', 'en': 'Aries', 'jp': '' },
    'taurus': { 'cn': '', 'en': 'Taurus', 'jp': '' },
    'gemini': { 'cn': '', 'en': 'Gemini', 'jp': '' },
    'cancer': { 'cn': '', 'en': 'Cancer', 'jp': '' },
    'leo': { 'cn': '', 'en': 'Leo', 'jp': '' },
    'virgo': { 'cn': '', 'en': 'Virgo', 'jp': '' },
    'libra': { 'cn': '', 'en': 'Libra', 'jp': '' },
    'scorpio': { 'cn': '', 'en': 'Scorpio', 'jp': '' },
    'sagittarius': { 'cn': '', 'en': 'Sagittarius', 'jp': '' },
    'capricorn': { 'cn': '', 'en': 'Capricorn', 'jp': '' },
    'aquarius': { 'cn': '', 'en': 'Aquarius', 'jp': '' },
    'pisces': { 'cn': '', 'en': 'Pisces', 'jp': '' }
}

export type Zodiac = keyof typeof ZodiacDict



